import React from 'react'
import NavBar from '../components/Navigation/NavBar'
import main from '../style/main'
import colors from '../style/colors'
import dragon from '../icons/dragon.svg'

const Hobbies = () => {
    return (
        <div style={main.appLayout}>
            <div style={Object.assign({}, main.row, {height: "100vh", display: "flex", alignItems: "center", paddingLeft: "2.5em", paddingRight: "6em", justifyContent: "space-between"})}>
                <NavBar/>
                <div style ={{display: "flex", flexDirection: "column", margin: "2%", height: "100%"}} >
                    <div style = {Object.assign({}, main.title, {color: colors.blue, marginTop: "2em"})}> Hobbies </div>
                    <div style = {main.subtitle}> Hi! Welcome to my Hobbies Page! </div>
                    <div style = {{fontSize: ".8em"}}> 
                        My goal with this page is to make it a resource for my friends and myself to keep track or record what is going on in our games.
                        I look forward to seeing how this page grows, and hope you will as well!
                    </div>
                    <div style = {main.row}>
                        <div style ={ {margin: "2em"}}>
                            <div style = {{display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "10em", height: "10em", background: colors.dragon}}>
                                <img src={dragon} alt = ""/>
                                <div>D&amp;D</div>
                            </div>
                        </div>

                        <div style ={ {margin: "2em"}}>
                            <div style = {{display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "10em", height: "10em", background: colors.dragon}}>
                                <img src={dragon} alt = ""/>
                                <div>Art</div>
                            </div>
                        </div> 

                        <div style ={ {margin: "2em"}}>
                            <div style = {{display: "flex", flexDirection: "column", justifyContent: "center", alignItems: "center", width: "10em", height: "10em", background: colors.dragon}}>
                                <img src={dragon} alt = ""/>
                                <div>Archery</div>
                            </div>
                        </div>
                    </div>  
                </div>
            </div>
        </div>
    );
}

export default Hobbies