import React from 'react'
import NavBar from '../components/Navigation/NavBar'
import main from '../style/main'

export const Home = () => {
    return (
    <div className="App">
      <div style={main.appLayout}>
        <div style = {Object.assign({}, main.center, main.row, {flex: 1})}>
            <div style = {main.title}>
                Angela Lam
            </div>
            <NavBar/>
        </div>
      </div>
    </div>
    );
}

export default Home