import React from 'react'
import {withRouter} from 'react-router-dom'
import buttons from '../../style/buttons/buttons'
import main from '../../style/main'
import about from '../../icons/about.svg'
import projects from '../../icons/projects.svg'
import hobbies from '../../icons/hobbies.svg'
import resume from '../../icons/resume.svg'
import pdf from '../../docs/2019 Angela Lam Resume.pdf'
import '../../style/buttons/buttons.css'

const NavBar = props => {
    const {history} = props
    
    const onClick = link => {
        history.push({link})
    };

    return (
        <div style = {buttons.bar}>
            <div style = {main.row}>
                <div style = {buttons.button} id = "yellow" className="button">
                    <img src={about} alt = "" className="button-icon"/>
                </div>
                <div style = {Object.assign({}, main.center, {fontSize:  ".8em"})}>About Me</div>
            </div>
            <div style = {main.row}>
                <div style = {buttons.button} id = "red" className="button">
                    <img src={projects} alt = "" className="button-icon"/>
                </div>
                <div style = {Object.assign({}, main.center, {fontSize:  ".8em"})}>Projects</div>
            </div>
            <div style = {main.row}>
                <div style = {buttons.button} id = "blue" className="button">
                    <img src={hobbies} alt = "" className="button-icon"/>
                </div>
                <div style = {Object.assign({}, main.center, {fontSize:  ".8em"})}>Hobbies</div>
            </div>
            <div style = {main.row}>
                <a href = {pdf} target = "_blank" rel="noopener noreferrer">
                    <div style = {buttons.button} id = "black" className="button">
                        <img src={resume} alt = "" className="button-icon"/>
                    </div>
                </a>
                <div style = {Object.assign({}, main.center, {fontSize:  ".8em"})}>Resume</div>
            </div>
        </div>
    );
}

export default withRouter(NavBar)